video {
    height: 100%;
    width: 100%;
    object-fit: cover;
    z-index: -1;
    position: fixed;
}

.banner {
    /* background: url('../images/img-home.jpg') cover center/cover no-repeat; */
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
}

.banner>h1 {
    color: #fff;
    font-size: 100px;
    margin-top: -100px;
}

.banner>p {
    color: #fff;
    margin-top: 8px;
    font-size: 32px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.banner-btns {
    margin-top: 32px;
}

.banner-btns .btn {
    margin: 6px;
}

.fa-play-circle {
    margin-left: 4px;
}


@media screen and (max-width: 960px) {
    .banner>h1 {
        font-size: 70px;
        margin-top: -150px;
    }
}

@media screen and (max-width: 768px) {
    .banner>h1 {
        font-size: 50px;
        margin-top: -100px;
    }

    .banner>p {
        font-size: 30px;
    }

    .btn {
        width: 100%;
        ;
    }
}